import {Component, EventEmitter, OnDestroy, OnInit, Output, ViewChild} from "@angular/core";
import {SourceView} from "../../../models/source-view.model";
import {AddSourceViewsFormComponent} from "./add-source-views-form/add-source-views-form.component";
import {ViewDataService} from "../../../services/user-view-source.service";
import { Router } from "@angular/router";
import { DummyDataStorage } from "../../../storage/data-storage";
import { Subscription } from "rxjs/Subscription";
import { ViewNameValidationResult } from "../../../models/validateviewname.model";
import { CacheModelStorage } from "../../../storage/cache.model.storage";
import { ViewDataModel } from "../../../models/user-view.model";


@Component({
  selector: 'app-add-view-select-source',
  templateUrl: './add-view-select-source.component.html',
  styleUrls: ['./add-view-select-source.component.css']
})
export class AddViewSelectSourceComponent implements OnInit, OnDestroy {


  @Output() selectedChanged = new EventEmitter<SourceView>(true);

  @ViewChild('fieldsFormComponent') fieldsFormComponent: AddSourceViewsFormComponent;

  viewModel: ViewDataModel;

  nameValidationResult: ViewNameValidationResult;

  viewModelDataChanged = new Subscription();
  viewModelDataValidationChanged = new Subscription();

  constructor(
    private viewDataService: ViewDataService,
	private dataStorage: DummyDataStorage,
	private cacheStorage: CacheModelStorage,
    private router: Router) {
  }

  ngOnInit() {
	  this.viewModelDataChanged = this.viewDataService.viewSettingModelChanged.subscribe(
		  (model: ViewDataModel) => {
			  this.viewModel = model;
		  });
	  this.viewModelDataValidationChanged = this.viewDataService.viewSettingModelValidationChanged.subscribe(
		  (result: ViewNameValidationResult) => {
			  this.nameValidationResult = result;
		  });

	  this.cacheStorage.retrieveUserViewsModel();
	  this.nameValidationResult = this.viewDataService.getViewNameValidationResult();
  }

    //To raise event from child component
  private onSelectedViewChanged(viewData: SourceView)
  {
    this.fieldsFormComponent.onAddSourceView(viewData);
  }

  private viewSettingsModelNameChanged()
  {
    //ToDo: refactor later
	  this.dataStorage.validateModelName(this.viewModel.name);
  }


  onBtnNext()
  {
	this.cacheStorage.storeUserViewsModel(this.viewModel);
    this.router.navigate(['addview/join']);
  }

  ngOnDestroy(): void {
    this.viewModelDataChanged.unsubscribe();
    this.viewModelDataValidationChanged.unsubscribe();

  }

}



