import { Component, EventEmitter, OnInit, Output, ViewChild, OnDestroy} from '@angular/core';
import {SourceView} from "../../../../models/source-view.model";
import {NodeEvent, TreeComponent} from "ng2-tree";
import {ViewType} from "../../../../models/enums.model";
import { ViewSourceService } from "../../../../services/view-source.service";
import { DummyDataStorage } from "../../../../storage/data-storage";
import { Subscription } from "rxjs/Subscription";
import { SourceViewData } from "../../../../models/sourceviewdata.model";
import { SettingsService } from "../../../../services/settings.service";

@Component({
	selector: 'app-source-views-selector',
	templateUrl: './source-views-selector.component.html',
	styleUrls: ['./source-views-selector.component.css']
})
export class SourceViewsSelectorComponent implements OnInit, OnDestroy {

	@Output() selectedChanged = new EventEmitter<SourceView>(true);

	@ViewChild('tree') tree: TreeComponent;

	private viewsDataChanged: Subscription;

	private selectedNodes: any[];

	nodes: SourceView;

	options = {
		useCheckbox: true,
		scrollOnSelect: false,
	};


	constructor(
		private service: ViewSourceService,
		private dataStorage: DummyDataStorage,
		private settingService: SettingsService) {
	}

	ngOnInit() {
		this.viewsDataChanged = this.service.sourceViewsChanged.subscribe(
			(data: SourceViewData[]) => {
				this.getTree(data);
			}
		);

		var sourceViews = this.service.getSourceViewsData();
		this.getTree(sourceViews);
	}

	private getTree(dataArray: SourceViewData[]) {
		const databaseName = this.settingService.getExistingSettings().databaseName;
		const top = new SourceView(databaseName, ViewType.category, 1, true, null);

		const rtTop = new SourceView('rt', ViewType.category, 11, true, top.id);
		const rhTop = new SourceView('rh', ViewType.category, 12, true, top.id);
		const rsysTop = new SourceView('rsys', ViewType.category, 13, true, top.id);

		const id = 100;
		for (let data of dataArray) {

			const item = new SourceView(data.name, ViewType.view, id, false, top.id);

			if (data.name.substring(0, 2) === 'rh') {
				item.parentId = 12;
				rhTop.children.push(item);
			}
			else if (data.name.substring(0, 2) === 'rt') {
				item.parentId = 11;
				rtTop.children.push(item);
			}
			else if (data.name.substring(0, 4) === 'rsys') {
				item.parentId = 13;
				rsysTop.children.push(item);
			}
		}


		top.children.push(rtTop, rhTop, rsysTop);

		this.nodes = top;
	}


	private emitSelectedEvent(e: NodeEvent): void {
		const nodeData = <SourceView>e.node.node;

		//Root or category item selected
		if (nodeData.category === ViewType.category) {
			return;
		}
		else if (nodeData.category === ViewType.view) {
			this.selectedChanged.emit(nodeData);
			console.log('Selection changed ', nodeData);
		}
		else if (nodeData.category === ViewType.field) {
			//ToDo: implement later
		}

	}

    refresh()
    {
	    this.dataStorage.getSourceViews();
    }

	ngOnDestroy() {
		this.viewsDataChanged.unsubscribe();
	}
}



