import {Component, OnDestroy, OnInit} from '@angular/core';
import { SettingsService } from "../../services/settings.service";
import { Subscription } from "rxjs/Subscription";
import { Settings } from "../../models/settings.model";
import { CacheSettingsStorage } from "../../storage/cache.settings.storage";

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit,OnDestroy {

	settings: Settings;

	private settingsChanged: Subscription;

	constructor(private settingsService: SettingsService
		, private settingsStorage: CacheSettingsStorage) { }

  ngOnInit() {
	  this.settingsChanged = this.settingsService.settingsChanged.subscribe((value: Settings) => {
		  this.settings = value;
	  });
	  this.settingsStorage.retrieveSettings();
  }

  ngOnDestroy() {
	  this.settingsChanged.unsubscribe();
  }

}



