import {SourceView} from "../models/source-view.model";
import {Subject} from "rxjs/Subject";
import {Injectable} from "@angular/core";
import { ViewType } from "../models/enums.model";
import { SettingsService } from "./settings.service";
import {SourceViewData} from '../models/sourceviewdata.model';



@Injectable()
export class ViewSourceService {

  sourceViewsChanged = new Subject();

  public sourceViews: SourceView;

	private sourceViewsData = [
		new SourceViewData('rt_patients', ['id', 'create_date']),
		new SourceViewData('rt_registers', ['id', 'create_date']),
		new SourceViewData('rt_cards', ['id', 'create_date']),
		new SourceViewData('rsys_employees', ['id', 'create_date']),
		new SourceViewData('rsys_settings', ['id', 'create_date']),
		new SourceViewData('rh_e_diagnosis', ['id', 'create_date']),
		new SourceViewData('rh_card_types', ['id', 'create_date'])
  ];

  public getSourceViewsData()
  {
    return this.sourceViewsData.slice();
  }

  public  constructor(private settingsService: SettingsService) {
    

  }

 public setSourceViews(sourceViewData: SourceViewData[])
 {
	 this.sourceViewsData = sourceViewData;
	 this.sourceViewsChanged.next(this.sourceViewsData.slice());
 }

 
}



