import {Injectable} from "@angular/core";
import { Settings} from "../models/settings.model";
import Utils from "../shared/utils.module";
import { ViewDataModel } from '../models/user-view.model';
import { ViewDataService } from "../services/user-view-source.service";

@Injectable()
export class CacheModelStorage
{
	constructor(private service: ViewDataService) { }

  storeUserViewsModel(model: ViewDataModel)
  {
	  sessionStorage.setItem('userViewModel', JSON.stringify(model));
	  this.service.setViewModel(model);
  }

  retrieveUserViewsModel()
  {
	  const modelText = sessionStorage.getItem('userViewModel');
	  let parsed: ViewDataModel;
	  if (Utils.isEmpty(modelText))
    {
		  parsed = new ViewDataModel();
    }
    else {
		  parsed = JSON.parse(modelText) as ViewDataModel;
	  }
	  this.service.setViewModel(parsed);
  }


}



