import {Component, Input, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {FormControl, NgForm} from "@angular/forms";
import {Settings} from "../../models/settings.model";
import { SettingsService} from "../../services/settings.service";
import {CheckConnectionResult} from "../../models/enums.model";
import { ActivatedRoute, Router } from "@angular/router";
import { Subscription } from "rxjs/Subscription";
import { DummySettingsStorage } from "../../storage/settings.storage";
import { CacheSettingsStorage } from "../../storage/cache.settings.storage";
import Utils from "../../shared/utils.module";
import { CheckConnection } from "../../models/checkconnection.model";

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.css']
})
export class SettingsComponent implements OnInit, OnDestroy {
		
 @ViewChild('f') settingsForm: NgForm;

  settings: Settings;

  connection: CheckConnection;

  isSaveValid = false;

  isConnectionTestBtnValid = false;

  private settingsChanged: Subscription;

  private connectionChanged: Subscription;

  constructor(
	  private settingsService: SettingsService,
	  private settingsCacheStorage: CacheSettingsStorage,
	  private settingsHttpStorage: DummySettingsStorage,
      private  router: Router)
  {

  }

  ngOnInit() {
	  this.settingsChanged = this.settingsService.settingsChanged.subscribe((value: Settings) => {
		  this.settings = value;
		  if (!this.settings.isInitialized)
		  {
			  this.settings.serverName = '10.0.0.5MSSQL2012,49270';
		  }
	  });

	  this.connectionChanged = this.settingsService.connectionChanged.subscribe((value: CheckConnection) => {
		  this.connection = value;
	  });
	  this.settingsCacheStorage.retrieveSettings();
	  this.settingsHttpStorage.checkConnection();

	  this.isConnectionTestBtnValid = this.settings.isInitialized;

  }

  userValueChanged()
  {
	  this.isSaveValid = false;
	  this.connection.result = CheckConnectionResult.none;
    this.isConnectionTestBtnValid =
		!Utils.isEmpty(this.settings.serverName) &&
	    !Utils.isEmpty(this.settings.userName) &&
	    !Utils.isEmpty(this.settings.password) ;

  }

  DatabaseSelectionChanged()
  {
	  this.isSaveValid =
		  this.connection.result === CheckConnectionResult.connected &&
      !Utils.isEmpty(this.settings.databaseName);
  }


 isCheckConnectionValid()
 {
	 return this.connection.result === CheckConnectionResult.connected;
 }

  checkConnection()
  {
	  this.settingsHttpStorage.checkConnection();
  }

  onSubmit(form: FormControl)
  {
	  this.settings.isInitialized = true;
	 this.settingsCacheStorage.storeSettings(this.settings);
     this.router.navigate(['']);
  }

  onClear()
  {
    this.settings.serverName = '';
    this.settings.password = '';
    this.settings.userName = '';
    this.settings.databaseName = '';
  }

  onExit()
  {
    this.router.navigate(['']);
  }


  ngOnDestroy() {
	  this.settingsChanged.unsubscribe();
	  this.connectionChanged.unsubscribe();
  }
}



