import {Treeitem} from "../shared/treeitem.model";
import {TreeModel} from "ng2-tree";
import {ViewType} from "./enums.model";

export class SourceView implements  Treeitem, TreeModel
{
  children: Treeitem[];

  constructor(
    public value: string,
    public category: ViewType,
    public id: number,
    public isExpanded: Boolean,
    public parentId?: number)
  {
    this.children = [];
  }

}



