import { Component, OnDestroy, OnInit, Output} from '@angular/core';
import {FormArray, FormControl, FormGroup, Validators} from "@angular/forms";
import {ViewDataModel} from "../../../../models/user-view.model";
import {ViewDataService} from "../../../../services/user-view-source.service";
import {SourceView} from "../../../../models/source-view.model";
import { UserSelectedView } from "../../../../models/user-selected-view.model";
import { Subscription } from "rxjs/Subscription";
import Utils from "../../../../shared/utils.module";
import { CacheModelStorage } from "../../../../storage/cache.model.storage";


@Component({
  selector: 'app-add-source-views-form',
  templateUrl: './add-source-views-form.component.html',
  styleUrls: ['./add-source-views-form.component.css']
})
export class AddSourceViewsFormComponent implements OnInit, OnDestroy {
	selectedViewsForm: FormGroup;

	viewModelChanged = new Subscription();

	selectedViewNames: string[];

	constructor(
		private viewDataService: ViewDataService,
		private cacheModelStorage: CacheModelStorage) {
    }

  ngOnInit() {
	  this.viewModelChanged = this.viewDataService.viewSettingModelChanged.subscribe(
		  (model: ViewDataModel) => {
		    this.selectedViewNames = [];
			  this.selectedViewNames = model.selectedViewNames;
			  this.initForm();
		  });

	  this.cacheModelStorage.retrieveUserViewsModel();
  }

  ngOnDestroy(): void {
	  this.viewModelChanged.unsubscribe();
  }

  onAddSourceView(view: SourceView)
  { let existedView = this.selectedViewNames.find(v => v === view.value);
    if (Utils.isEmpty(existedView)) {
      this.addSourceView(view.value);
      this.selectedViewNames.push(view.value);
    }
	  //this.viewDataService.setViewModelViews(this.selectedViewNames);
  }

  onDeleteView(number: number) {
	  (<FormArray>this.selectedViewsForm.get('selectedViews')).removeAt(number);
	  this.selectedViewNames = this.selectedViewNames.splice(number,1);
	  //this.viewDataService.setViewModelViews(this.selectedViewNames);
  }

  private addSourceView(viewName: string) {

		  const nameCtrl = new FormControl(null);
		  const groupCtrl = new FormGroup({
			  'name': nameCtrl
		  });
		  nameCtrl.patchValue(viewName);
		  (<FormArray>this.selectedViewsForm.get('selectedViews')).push(groupCtrl);
  }



  private  initForm()
  {
    let selectedViews = new FormArray([]);
    this.selectedViewsForm = new FormGroup({
      'selectedViews': selectedViews
    });

    for (let selected of this.selectedViewNames)
    {
      console.log('Adding ',selected, ' to form: ', this);
      this.addSourceView(selected);
    }
    //ToDo: load data from cache



  }

  private  getFormControls(controlGroup: string)
  {
    return (<FormArray>this.selectedViewsForm.get(controlGroup)).controls;
  }

  onSubmit() { }
}



