import {Injectable} from "@angular/core";
import {Settings} from "../models/settings.model";
import {CheckConnectionResult} from "../models/enums.model";
import { Subject } from "rxjs/Subject";
import { CheckConnection } from "../models/checkconnection.model";

@Injectable()
export  class  SettingsService
{
  private settings: Settings;

  private connection: CheckConnection;

  public settingsChanged = new Subject();

  public connectionChanged = new Subject();

   public  getExistingSettings(): Settings
   {
	   return this.settings;
  }

   public getExistingConnection(): CheckConnection{
	   return this.connection;
   }


   public setCheckConnection(connection: CheckConnection )
   {
	   this.connection = connection;
	   this.connectionChanged.next(this.connection);	   
   }

   public setSettings(settings: Settings) {
	   this.settings = settings;
	   this.settingsChanged.next(this.settings);
   }
}



