import {NgModule} from '@angular/core';
import {HeaderComponent} from "./header/header.component";
import { DataStorage, DummyDataStorage} from "../storage/data-storage";
import {RoutingModule} from "../routing.module";
import { SettingsComponent } from './settings/settings.component';
import {SettingsService} from "../services/settings.service";
import { CommonModule } from '@angular/common';
import { SettingsStartComponent } from './settings/settings-start/settings-start.component';
import {RootRoutingModule} from "./root-routing.module";
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {Router} from "@angular/router";
import { AddViewSelectSourceComponent } from './add-new-view/select-source-views/add-view-select-source.component';
import { SourceViewsSelectorComponent } from './add-new-view/select-source-views/source-views-selector/source-views-selector.component';
import {ViewSourceService} from "../services/view-source.service";
import {HttpClient, HttpHandler} from "@angular/common/http";
import {TreeModule} from "ng2-tree";
import { AddSourceViewsFormComponent } from './add-new-view/select-source-views/add-source-views-form/add-source-views-form.component';
import {ViewDataService} from "../services/user-view-source.service";
import { AddNewViewCommonComponent } from './add-new-view/add-new-view-common/add-new-view-common.component';
import { SelectedViewsTreeComponent } from './add-new-view/add-join-operations/selected-views-tree/selected-views-tree.component';
import { AddJoinOperationsComponent } from './add-new-view/add-join-operations/add-join-operations/add-join-operations.component';
import { SetJoinSettingsFormComponent } from './add-new-view/add-join-operations/set-join-settings-form/set-join-settings-form.component';
import { CacheSettingsStorage } from "../storage/cache.settings.storage";
import { DummySettingsStorage } from "../storage/settings.storage";
import { CacheModelStorage } from "../storage/cache.model.storage";

@NgModule({
	imports: [
    CommonModule,
    RootRoutingModule,
    FormsModule,
    TreeModule,
    ReactiveFormsModule
  ],
  declarations: [
    HeaderComponent,
    SettingsComponent,
    SettingsStartComponent,
    AddViewSelectSourceComponent,
    SourceViewsSelectorComponent,
    AddSourceViewsFormComponent,
    AddNewViewCommonComponent,
    SelectedViewsTreeComponent,
    AddJoinOperationsComponent,
	SetJoinSettingsFormComponent
  ],
  exports: [
	  HeaderComponent
  ],
  providers: [
	  DummyDataStorage,
	  DummySettingsStorage,
	 
      SettingsService,
      ViewSourceService,
	  ViewDataService,
	  CacheSettingsStorage,
	  CacheModelStorage
  ]
})
export class RootModule { }



