import {Injectable} from "@angular/core";
import {ViewDataModel} from "../models/user-view.model";
import {DataStorage} from "../storage/data-storage";
import {DummyDataStorage} from "../storage/data-storage";
import { ViewSourceService} from "./view-source.service";
import { UserSelectedField } from "../models/user-selected-field.model";
import { CacheModelStorage } from "../storage/cache.model.storage";
import { ViewNameValidationResult } from "../models/validateviewname.model";
import { Subject } from "rxjs/Subject";
import { UserSelectedView } from "../models/user-selected-view.model";

@Injectable()
export class ViewDataService {

  private viewNameValidationResult: ViewNameValidationResult;

  private viewSettingsModel: ViewDataModel;

  public viewSettingModelChanged = new Subject<ViewDataModel>();

  public viewSettingModelValidationChanged = new Subject<ViewNameValidationResult>();

  public constructor(

    private sourceViewService: ViewSourceService) {

	  this.viewNameValidationResult = new ViewNameValidationResult(false, 'Результат проверки еще не иницилизирован');
  }

  public getViewDataSettings() {
	  return this.viewSettingsModel;
  }

  public fillViewDataWithFields() {
    const sourceViewsData = this.sourceViewService.getSourceViewsData();
    for(const selectedView of this.viewSettingsModel.selectedViews)
    {
      const sourceView = sourceViewsData.find(v => v.name === selectedView.name);
      selectedView.fields = [];
      for(const sourceField of  sourceView.fields)
      {
        const userField = new UserSelectedField(sourceField, selectedView.name);
        selectedView.fields.push(userField);
      }
    }
  }


  public getViewNameValidationResult(): ViewNameValidationResult{
	  return this.viewNameValidationResult;
  }

  public setViewModelNameValidationResult(result: ViewNameValidationResult) {
	  this.viewNameValidationResult = result;
	  if (!result.result) {
		  this.viewSettingsModel.isValid = false;
	  }
	  this.viewSettingModelValidationChanged.next(this.viewNameValidationResult);
  }

  public setViewModel(model: ViewDataModel) {
	  this.viewSettingsModel = model;
	  this.viewSettingModelChanged.next(this.viewSettingsModel);
  }

  public setViewModelViews(selectedViewNames: string[])
  {
      //ToDo: implement logic to store existed settings and create new useritems
	  this.viewSettingsModel.selectedViewNames = selectedViewNames;
	  this.viewSettingModelChanged.next(this.viewSettingsModel);
  }

  public clear() {
	  this.viewSettingsModel.name = '';
	  this.viewSettingsModel.selectedViews = [];
	  this.viewSettingModelChanged.next(this.viewSettingsModel);
  }

}



