import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { SettingsService } from "../services/settings.service";
import { CheckConnectionResult } from "../models/enums.model";
import 'rxjs/Rx';
import { CheckConnection } from "../models/checkconnection.model";

@Injectable()
export class SettingsStorage
{
	constructor(
		private httpClient: HttpClient,
		private settingsService: SettingsService)
	{

	}

	checkConnection() {
		this.httpClient.get<any>('type url here',
			{
				observe: 'body'
			}).
			map(

			(result) => {
				console.log(result);
				return new CheckConnection(result.state, result.databases, result.failMessage);
			}).
			subscribe(
			(connection: CheckConnection) => {
				this.settingsService.setCheckConnection(connection);
			}
			);
	}

}

@Injectable()
export class DummySettingsStorage {
	constructor(
		private settingsService: SettingsService) {

	}

	checkConnection() {
		this.settingsService.setCheckConnection(new CheckConnection(
			CheckConnectionResult.connected,
			['database1', 'database2', 'database3']
		));
	}

}



