import { Component, OnInit } from '@angular/core';
import {DummyDataStorage} from "../../../storage/data-storage";
import { ViewDataService } from "../../../services/user-view-source.service";
import { CacheModelStorage } from "../../../storage/cache.model.storage";
import { ViewDataModel } from "../../../models/user-view.model";
import { Subscription } from "rxjs/Subscription";

@Component({
  selector: 'app-add-new-view-common',
  templateUrl: './add-new-view-common.component.html',
  styleUrls: ['./add-new-view-common.component.css']
})
export class AddNewViewCommonComponent implements OnInit {

	viewModel: ViewDataModel;

	viewModelDataChanged = new Subscription();
  constructor(private viewDataService: ViewDataService,
	  private dataStorage: DummyDataStorage,
	  private cacheStorage: CacheModelStorage) { }

  ngOnInit() {
	  this.viewModelDataChanged = this.viewDataService.viewSettingModelChanged.subscribe(
		  (model: ViewDataModel) => {
			  this.viewModel = model;
		  });
	  this.cacheStorage.retrieveUserViewsModel();
  }

  onCreateView()
  {

  }

  onSaveSettings()
  {
	  let model = this.viewDataService.getViewDataSettings();
	  this.cacheStorage.storeUserViewsModel(model);
  }

  onClear()
  {
    this.viewDataService.clear();
  }
}



