import {Injectable} from "@angular/core";
import {HttpClient, HttpParams, HttpRequest} from '@angular/common/http';
import {ViewSourceService} from "../services/view-source.service";
import 'rxjs/Rx';
import {SourceView} from "../models/source-view.model";
import {SettingsService} from "../services/settings.service";
import { ViewDataService } from "../services/user-view-source.service";
import { ViewNameValidationResult } from "../models/validateviewname.model";
import Utils from "../shared/utils.module";
import { SourceViewData } from "../models/sourceviewdata.model";


@Injectable()
export class DataStorage {
  header = new Headers({'Content-Type': 'application/json'});

  constructor(
    private httpClient: HttpClient,
    private viewSourceService: ViewSourceService,
    private settingsService: SettingsService,
    private userViewSettingsService: ViewDataService
  ) {

  }

  getSourceViews()
  {
	  this.httpClient.get<SourceViewData[]>('type url here',
      {
        observe: 'body'
      }).
    map(

		(views: SourceViewData[]) => {
        console.log(views);
        return views;
      }).
    subscribe(
      (views: SourceViewData[]) => {

        this.viewSourceService.setSourceViews(views);
      }
    );
  }

  public validateViewName(viewName: string)
  {
    //ToDO: to backend?
	  let result;
	  if (Utils.isEmpty(viewName)) {
		  result = { 'result': false, 'failedReason': 'Имя модели не может быть пустым!' };
		  this.userViewSettingsService.setViewModelNameValidationResult(result);
	  }
	  else if (viewName.length < 5) {
		  result = { 'result': false, 'failedReason': 'Имя модели не может быть короче чем 5 символов!' };
		  this.userViewSettingsService.setViewModelNameValidationResult(result);
	  }
	  else if (viewName.substr(0, 3) != 'hm_') {
		  result = { 'result': false, 'failedReason': 'Имя модели должно начинаться с hm_!' };
		  this.userViewSettingsService.setViewModelNameValidationResult(result);
	  }
	  else {

      this.httpClient.get<any>('type url here',
        {
          observe: 'body'
        }).map(
        (result) => {
          return {'result': result.value, 'failedReason': result.message};
        }).subscribe((result: ViewNameValidationResult) => {
			this.userViewSettingsService.setViewModelNameValidationResult(result);
      });
    }
  }

}

@Injectable()
export class DummyDataStorage {

	constructor(
		private viewSourceService: ViewSourceService,
		private settingsService: SettingsService,
		private userViewSettingsService: ViewDataService) {
	}

	getSourceViews() {
		
	}

	validateModelName(modelName: string) {
		let result;
		if (Utils.isEmpty(modelName)) {
			result = { 'result': false, 'failedReason': 'Имя модели не может быть пустым!' };
		}
		else if (modelName.length < 5) {
			result = { 'result': false, 'failedReason': 'Имя модели не может быть короче чем 5 символов!' };
		}
		else if (modelName.substr(0, 3) != 'hm_') {
			result = { 'result': false, 'failedReason': 'Имя модели должно начинаться с hm_!' };
		}
		else {
			result = { 'result': true, 'failedReason': '' };
		}
		this.userViewSettingsService.setViewModelNameValidationResult(result);
	}
}


